/* --- buttons --- */
.btn {
  margin: 8px; }

.btn {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 2px;
  font-weight: 400;
  line-height: 1;
  text-align: center;
  text-decoration: none;
  overflow: hidden;
  z-index: 0;
  cursor: pointer;
  transition: all 0.2s ease-in-out; }
  .btn:active {
    transition: none; }
  .btn:after {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    overflow: hidden;
    width: 0;
    height: 100%;
    opacity: 0;
    z-index: -1;
    transform: translateX(-50%);
    transition: translateX 0.3s, width 0.3s, opacity 0.3s, background-color 0.3s; }
  .btn:hover:after, .btn.active:after {
    width: calc(100% + 2px);
    opacity: 1; }
  .btn.active {
    cursor: default; }
  .btn > span {
    flex: 1; }

