/**

1)	При клике на кнопку «Add Property» добавляется нумерованная строка с селектом (список свойств), иконкой сортировки и кнопкой «Удалить строку».
    Важно! Property может быть выбрано только один раз.
    При добавлении/удалении строки, список в селекте должен обновиться (option должен либо удаляться с селекта, либо становиться disabled).
2)	При клике на «Sort» происходит отправка формы. Отправляться должен JSON массив,
    в котором должны передаваться поля «Order (ASC/DESC), Property,  Priority(цифра, которая выводится слева в таблице).
3)	При клике на «крестик» строка должна удаляться
4)	При клике на иконку в столбце «Order» должен меняться параметр «Order» для конкретной строки. Он может быть ASC или DESC.

*/
